.header {
  margin: 0 auto;
  text-align: center;
  grid-area: header;
  box-shadow: inset 0 -3em 3em rgba(0,0,0,0.2), 0 0 0 1px rgba(0,173,176,0.553), 0.3em 0.3em 1em rgba(0,0,0,0.2);
  border-radius: 10px;
}
.logo {
  margin-right: 10px;
  margin-left: 8px;
  margin-top: 4px;
  margin-bottom: 4px;
  width: 80px;
  height: 80%;
  border-radius: 40px;
}
