.card {
  margin: 4px 10px;
  text-align: center;
  box-shadow: inset 0 -1em 1em rgba(0,0,0,0.2), 0 0 0 1px rgba(0,173,176,0.553), 0.1em 0.1em 0.1em rgba(0,0,0,0.3);
  outline: 1px solid rgba(0,173,176,0.553);
  border-radius: 4px;
  position: relative;
  overflow: hidden;
  transition: 0.5s;
  max-width: 267px;
  width: 100%;
}
.card__link-wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  direction: ltr;
}
.card__link {
  direction: ltr;
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  max-height: max-content;
  overflow: hidden;
  margin-right: 10px;
  justify-content: flex-start;
  text-decoration: none;
  width: 100%;
}
.card__link-img {
  direction: ltr;
  padding: 0;
  margin: 0;
  max-height: 100%;
  overflow: hidden;
  margin-right: 10px;
  text-decoration: none;
  width: 36%;
}
.card__button-delete {
  position: absolute;
  right: 5px;
  bottom: 5px;
  background: transparent url("../../images/delete-icon.svg") no-repeat;
  background-size: 14px 14px;
  border: none;
  min-height: 14px;
  color: #d7d7d7;
  cursor: pointer;
}
.card__button-is-favorite-size {
  position: absolute;
  right: 15px;
  bottom: 15px;
  background: transparent url("../../images/favorite.svg") no-repeat;
  background-size: 24px 24px;
  border: none;
  min-height: 24px;
  width: 24px;
  color: #f24e1e;
  transition: 0.5s;
  cursor: pointer;
}
.card__button-is-notFavorite {
  position: absolute;
  right: 15px;
  bottom: 15px;
  background: transparent url("../../images/Vector.svg") no-repeat;
  background-size: 24px 24px;
  border: none;
  min-height: 24px;
  width: 24px;
  color: #f24e1e;
  transition: 0.5s;
  cursor: pointer;
}
.card__img {
  display: inline-block;
  margin-right: 10px;
  margin-left: 8px;
  margin-top: 4px;
  margin-bottom: 4px;
  width: 48px;
  height: 48px;
  border-radius: 24px;
}
.card__title {
  display: inline-block;
  margin: 0;
  margin-right: 10px;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  color: #fff;
  max-height: max-content;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  text-decoration: none;
}
